"use client";

import { useEffect, useRef } from "react";
import * as d3 from "d3";

const KnowledgeGraph = ({ graphData }) => {
  const svgRef = useRef();

  useEffect(() => {
    if (!graphData) return;

    const svg = d3.select(svgRef.current);
    svg.selectAll("*").remove();

    const { nodes, links } = graphData;

    const width = 800;
    const height = 600;

    const zoom = d3.zoom().on("zoom", (event) => {
      svgGroup.attr("transform", event.transform);
    });

    svg.call(zoom).on("dblclick.zoom", null); 

    const svgGroup = svg.append("g");

    const simulation = d3
      .forceSimulation(nodes)
      .force(
        "link",
        d3
          .forceLink(links)
          .id((d) => d.id)
          .distance((d) => {
            const maxDistance = 1000; // maximum distance between nodes (lower weight)
            const minDistance = 20;  // minimum distance between nodes (higher weight)
            const weight = d.weight; 

            //exponential decay for distance
            return minDistance + (maxDistance - minDistance) * Math.exp(-3 * weight);
          })
      )
      .force("charge", d3.forceManyBody().strength(-400))
      .force("center", d3.forceCenter(width / 2, height / 2));

    const link = svgGroup
      .append("g")
      .attr("class", "links")
      .selectAll("line")
      .data(links)
      .enter()
      .append("line")
      .style("stroke", (d) => {
        const weight = d.weight;
        return d3.interpolateBlues(Math.pow(weight, 3)); // blue link color scales exponentially
      })
      .style("stroke-width", 2);

    const node = svgGroup
      .append("g")
      .attr("class", "nodes")
      .selectAll("circle")
      .data(nodes)
      .enter()
      .append("circle")
      .attr("r", 10)
      .style("fill", "skyblue")
      .call(
        d3
          .drag()
          .on("start", (event, d) => {
            if (!event.active) simulation.alphaTarget(0.3).restart();
            d.fx = d.x;
            d.fy = d.y;
          })
          .on("drag", (event, d) => {
            d.fx = event.x;
            d.fy = event.y;
          })
          .on("end", (event, d) => {
            if (!event.active) simulation.alphaTarget(0);
            d.fx = null;
            d.fy = null;
          })
      );

    const label = svgGroup
      .append("g")
      .attr("class", "labels")
      .selectAll("text")
      .data(nodes)
      .enter()
      .append("text")
      .attr("dx", 12)
      .attr("dy", 4)
      .text((d) => d.id);

    simulation.on("tick", () => {
      link
        .attr("x1", (d) => d.source.x)
        .attr("y1", (d) => d.source.y)
        .attr("x2", (d) => d.target.x)
        .attr("y2", (d) => d.target.y);

      node.attr("cx", (d) => d.x).attr("cy", (d) => d.y);

      label.attr("x", (d) => d.x).attr("y", (d) => d.y);
    });
  }, [graphData]);

  return <svg ref={svgRef} width={800} height={600} style={{ border: "1px solid #ddd" }}></svg>;
};

export default KnowledgeGraph;
